.search-results {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
}

.back-button {
  background: #f8f9fa;
  border: 1px solid #dadce0;
  border-radius: 4px;
  color: #202124;
  font-size: 14px;
  padding: 8px 16px;
  margin-bottom: 20px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.back-button:hover {
  background: #f1f3f4;
}

.results-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.result-card {
  background: white;
  border: 1px solid #dadce0;
  border-radius: 8px;
  padding: 16px;
  transition: box-shadow 0.2s;
}

.result-card:hover {
  box-shadow: 0 1px 6px rgba(32,33,36,0.28);
}

.result-meta {
  color: #70757a;
  font-size: 14px;
  margin-bottom: 8px;
}

.result-content {
  color: #202124;
  font-size: 16px;
  line-height: 1.6;
}

.result-content p {
  margin: 4px 0;
}

.result-triggers {
  color: #1a73e8;
  font-size: 14px;
  margin-top: 12px;
} 